/* Style for iphone first */
html {
  scroll-behavior: smooth;
}

#down-arrow a {
  color: white;
}

.footer-icons a {
  color: white;
}

#me-div {
  text-align: center;

}

#me {
  width: 20%;
  height: auto;
  border-radius: 0.3em;
  transform: rotate(270deg);
  margin-bottom: 6vh;
  border: solid;
}

.header {
  min-height: 80vh;
  padding: 5%;
  background-image: linear-gradient(to right, red , pink, yellow);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  -moz-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
}

#my-name, #react-typing-effect, #resume {
  letter-spacing: 4px;
  text-align: center;
  color: white;
}

#my-name {
  /* margin-top: 25px; */
  font-family: 'Questrial', sans-serif;
  font-size: 20px;
}


#resume {
  margin-top: 25px;
  font-family: 'Questrial', sans-serif;
}

#resume a:link, a:visited {
  background-color: #f44336;
  color: white;
  padding: 14px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 13px;
}

#resume a:hover, a:active {
  background-color: red;
}

.social-icons {
  text-align: center;
  margin-top: 10px;
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.social-icons i {
    margin-right: 1.0%;
    margin-left: 1.0%;
    color: white;
}

#down-arrow {
  color: white;
  text-align: center;
  padding-top: 150px;
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}
/* end header styles now into container */
.container {
  margin-left: 5%;
  margin-top: 2%;
  background-color: white;
  overflow: auto;
}

#projects-title, #blogs-title, #skills-title {
  font-family: 'Questrial', sans-serif;
  font-weight: bold;
  /* text-align: left;
  margin: 10px; */
  display: block;
  font-size: 1.0em;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
  letter-spacing: 2px;
  margin-top: 20px;
}

.title {
  font-family: 'Questrial', sans-serif;
  font-size: 1em;
  font-weight: bold;
}

.frameworks {
  text-decoration: none;
  font-size: 11px;
  color: #4b606d;
  font-weight: bolder;
  font-family: 'Questrial', sans-serif;
}

.bullet {
  padding: 0px 2px;
}

.description {
  font-size: 0.9rem;
  margin-bottom: 6px;
}

.links {
  display: flex;
}

.links a:link, a:visited {
  background-color: #f44336;
  color: white;
  padding: 4px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 11px;
  border-radius: 5px;
  margin: 0px 2px;
}

.project-container {
  margin-bottom: 15px;
}

.blog-posts {
  font-size: 12px;
  margin-right: 2px;
}

.blog :last-child {
  margin-bottom: 0%;
}

.blog-posts > a {
  text-decoration: none;
  margin-left: 1.5%;
  /* color: #FF4343; */
  color: #4b606d;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: bold;
  font-size: 12px;
}

.skill-type, .school-name {
  font-family: 'IBM Plex Mono', monospace;
  font-weight: bolder;
  /* color: rgb(246, 76, 65); */
  font-size: 1.2rem;
  background: -webkit-linear-gradient(red, rgb(246, 76, 65), grey);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
 margin-bottom: 0px;
}

.s1 .skill, .s2 .skill, .s3 .skill, .s4 .skill {
  margin: auto;
  line-height: 1.4285em;
  font-size: small;
}

footer {
  /* background: rgba(65, 89, 246, 1); */
  background: linear-gradient(#FA4F4F, white);
  background-size: 400% 400%;
  color: white;
  text-align: center;
  padding: 10px;
  margin-top: 15px;
}

.footer-icons {
  font-size: 16px;
  margin-bottom: 12px;
}

/* MEDIA QUERIES FOR IPAD AND DESKTOP SO FAR */
/* ipad */
@media only screen and (min-width: 768px) {
  .skills {
    display: flex;
  }

  .skills > div {
    width: 20%;
    padding-right: 2.5%;
    margin-right: 2.5%;
    border-right: 2px solid rgba(168, 168, 168, .3);
  }

  .skill-type, .school-name {
    ‘font-family: 'IBM Plex Mono', monospace;
    font-weight: bolder;
    /* color: rgb(246, 76, 65); */
    font-size: 1.2rem;
    background: -webkit-linear-gradient(red, rgb(246, 76, 65), grey);
   -webkit-background-clip: text;
   -’webkit-text-fill-color: transparent;
  }
}

/* desktop */
@media only screen and (min-width: 1000px) {

  #my-name {
    font-size: 35px;
  }

  #resume a:link, a:visited {
    font-size: 15px;
  }

  #projects-title, #blogs-title, #skills-title {
    font-size: 2em;
  }

  .title {
    font-size: 1.5em;
  }

  .project-header {
    display: flex;
  }

  .frameworks {
    font-size: 1.2em;
  }

  .title {
    margin-right: 15px;
  }

  .description {
    font-size: 1.2rem;
    margin-bottom: 6px;
    margin-top: 2px;
    /* font-style: italic; */
  }

  .blog-posts a {
    font-size: 1.3rem;
  }

  .blog-posts {
    font-size: 1.3rem;
    margin-bottom: 2px;
  }

  .links {
    display: flex;
    margin-bottom: 2rem;
  }

  .links a:link, a:visited {
    padding: 10px 16px;
    font-size: 12px;
    font-weight: bold;
  }

  .skill-type {
    font-size: 1.4rem;
    margin-bottom: 3px;
  }

  .s1 .skill, .s2 .skill, .s3 .skill, .s4 .skill {
    margin: auto;
    line-height: 1.4285em;
    font-size: medium;
  }

  #projects-title, #blogs-title, #skills-title {
    font-family: 'Questrial', sans-serif;
    font-weight: bold;
    display: block;
    font-size: 1.7em;
    margin-block-start: 0.83em;
    letter-spacing: 2px;
  }

  .footer-icons {
    font-size: 35px;
  }

}
